
table td{
    border: thin solid gainsboro;
}
#search_glass{
    width: 7%;
    height: 30px;
    float: right;
    margin-top: 6px;
}
 #search_text_field{
     border: none;
     font-size: large;
     width: 546px;
     height: 36px;
     font-family: "Source Sans Pro", Arial, sans-serif;
     border-radius: 4px;
 }
#search_glass:hover{
    cursor: pointer;


}


#search_box{
    background-color: gainsboro;
    width: 600px;
    height: 36px;
    font-size: medium;
    border-radius: 4px;
    border: thin solid #ffffff;
    box-shadow: 0 0 1px 1px gainsboro;
    font-family: "Source Sans Pro", Arial, sans-serif;
    text-indent: 3px;
    margin-bottom: 30px;
    margin-left:26%;

}
#search_box:hover{
    background: #7bbffb;

}
.objects_entry_point{
    border:none ;


}
#items{
    margin-top: 200px;
    height: 140px;
    width: 848px;
    padding: 20px;
    border-radius: 2px;
    background: white;
    margin-left: 24%;
    box-shadow: 0 0 4px 0 gainsboro;

}
#footer{

    bottom: 0;
    position: absolute;
    font-size: 10px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell,
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;

}
#scroll_list{

      width: 100%;
      height: 500px;
      margin-top: 100px;
      margin-left: -10px;
      position: fixed;
      border-bottom: thin solid gainsboro ;
      overflow-y: scroll;

}
#menu{

    padding: 10px 15px;
    margin-right: 6px;
    background: #7bbffb;
    border-radius: 2px;
    float: right;

}
#menu:hover{
    cursor: pointer;
    color: white;

}
@media screen and (max-width: 700px)  and (orientation: portrait) {
    #search_box {

        width: 200%;
        margin-left: -4px;
        height: 36px;
    }

    #shrink_size{

        margin-left: -8px;

    }
    .table_header{
        font-size: 12.348px;
        width: 20px;


    }
}
 #heading_background{

     box-shadow: 0 1px 0 0  gainsboro;

     position: fixed;
     top: -5px;
     width: border-box;
     left: 0;
     right: 0;
     background-color: #2a2a2a;
     padding-top: 20px;

 }
 #menus_list{
     text-align: center;
     margin-left: 42px;

 }
 .menu_list{
     font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell,
     "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
     color: #e2e2e2;
     padding-right: 7%;
     font-size: 18px;




 }
.table_header{
    font-family: "Source Sans Pro", Arial, sans-serif;
    color: #a3a3a3;
    text-align: left;
    width: 10%;
    padding-bottom: 40px;
    padding-top: 10px;






}
.menu_list:hover{
    cursor: pointer;
    transition-timing-function: cubic-bezier(8,9,0,9);
    color: #7bbffb;
}